footer {
  width: auto;
  height: 560px;
  background-image: url(../../img/bgcolor_3.png);
  display: flex;
  gap: 86px;
}
.footer_info {
  width: 198px;
  padding-top: 140px;
  padding-left: 370px;
}
.footer_block a {
  text-decoration: none;
  color: white;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 34px;
}

.footer_info__number {
  display: inline-block;
  color: white;
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 600;
  padding-top: 53px;
  padding-bottom: 22px;
}
.footer_info__adress {
  display: inline-block;
  color: white;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 500;
}
.footer_info__email {
  display: inline-block;
  color: white;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 500;
  padding-top: 22px;
  padding-bottom: 48px;
}
.footer_info_social {
  width: 220px;
}
.footer_info_social img {
  display: inline-block;
  padding-right: 20px;
}
.footer_info__confid {
  display: inline-block;
  color: rgb(189, 189, 189);
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 400;
  margin-top: 40px;
}
.footer_block {
  display: flex;
  flex-direction: row;
  gap: 60px;
  padding-top: 70px;
}
.footer_block_child {
  width: 150px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.footer_block_teen {
  width: 267px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.footer_block_adult {
  width: 186px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.footer_block_info {
  width: 103px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.footer_block span {
  color: white;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 34px;
}

.footer_block__title {
  color: white;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 700;
}
.footer_block_text {
  display: flex;
  flex-direction: column;
  gap: 1px;
}
@media all and (max-width: 1575px) {
  .footer_info {
    padding-left: 100px;
  }
}
@media all and (max-width: 1310px) {
  .footer_block,
  .footer {
    gap: 15px;
  }
  .footer_info {
    padding-left: 30px;
  }
}
@media all and (max-width: 1160px) {
  footer {
    gap: 10px;
  }
}
@media all and (max-width: 1020px) {
  .footer_block {
    display: none;
  }
}
